





























































































































@import url('https://fonts.googleapis.com/css?family=Lato');

body{
	background-color: #1f1c43;
}

html{
  @media(max-width: 414px) {
    min-width: 103vw;
  }
}

#app {
  font-family: 'Lato', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

html, body, pre, code, kbd, samp, button, a, input{
	font-family: 'Lato', cursive;
}

.navbar{
  background-color: #0f0d2a;
  height: 130px;
  ::v-deep .navbar-burger{
    color: white;
    position: relative;
    top: 30px;
    left: 10px;
  }
  &.nav-fixed{
    position: fixed;
    width: 100%;
    top: 0px;
  }
}

.footer{
  width: 100%;
  background-color: #0f0d2a;
  color: white;
  padding: 40px 20px;
  font-size: 12px;
  margin-top: 30px;
  p, strong{
    color: white;
    text-transform: uppercase;
    font-family: 'Lato', cursive;
  }
}


.navbar-brand{
  height: 110px;
  width: 25%;
  position: relative;
  top: 10px;
  @media(max-width: 1096px) {
    width: 90%;
  }
  .navbar-item{
    padding-left: 10%;
    max-width: 95%;

    @media(max-width: 1096px) {
      max-height: 90%;
    }
  }
  img{
    max-height: 90%;
    display: block;
    margin: auto;
    box-sizing: unset;
    position: relative;
    left: 0;

  }
}

.navbar-menu{
  background-color: #0f0d2a;
  .navbar-item{
    font-size: 17px;
    padding: 25px 15px;
    text-transform: uppercase;
    color: gray;
    i{
      color: #00d8cd;
      opacity: 0.6;
    }
    &.router-link-active{
      color: white;
      background: inherit;
      i{
        color: #00d8cd
      }
    }
    &:hover{
      background-color: #0f0d2a !important;
    }
    .ditem{
      text-transform: capitalize !important;;
    }
  }
  .close-btn{
    background-color: transparent;
    color: white;
    display: inline-block;
    padding: 0px;
    span{
      font-size: 11px;
      position: relative;
      top: 6px;
    }
  }
}

.user-name{
  font-size: 12px;
  position: relative;
  right: 40%;
  top: 20px;
  .user-text{
    text-overflow: ellipsis;
    overflow: hidden;
    width: 150px;
    display: inline-block;
    white-space: nowrap;
    position: relative;
    top: 10px;
  }
  .icon{
    position: relative;
    left: 20px;
    top: 5px;
  }
}

#landing-navbar{
  .navbar-start .navbar-item{
    display: block;
    span{
      position: relative;
      top: 25px;
        @media(max-width: 414px) {
        top: 0px;
      }
    }
  }
  .tile.no-m{
    margin-bottom: 5px !important;
  }
  .buttons .close-btn{
    display: block;
    position: relative;
    bottom: 10px;
    width: 100%;
    text-align: right;
  }
  .user-name{
    font-size: 14px;
    display: block;
    width: 100%;
    left: 0;
    top: 0px;
  }
}


